import React, { useEffect } from "react";
import { toast } from "react-toastify";
import { Link } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";

import AdminNavs from "../../components/Admin/AdminNavs";
import { OrderDeliveredDelete, OrdersList } from "../../actions/orderAction";

const AdminOrderList = ({ history }) => {
  const dispatch = useDispatch();
  const { loading, error, orders } = useSelector((state) => state.orderList);
  const { success, loading: DeleteLoading } = useSelector(
    (state) => state.orderDelete
  );
  const user = useSelector((state) => state.user);

  const deleteHandler = (id) => {
    if (window.confirm("This may help you in future")) {
      dispatch(OrderDeliveredDelete(id));
    }
  };

  useEffect(() => {
    if (user && user.isAdmin) {
      dispatch(OrdersList());
    } else {
      history.push("/login");
    }
  }, [dispatch, history, user, success]);

  return (
    <>
      <AdminNavs />

      <header aria-label="Page Header" className="bg-gray-50">
        <div className="mx-auto max-w-screen-xl px-4 py-8 sm:px-6 lg:px-8">
          <div className="mt-8 pb-10">
            <h1 className="text-2xl font-bold text-gray-900 sm:text-3xl">
              All your orders
            </h1>

            <p className="mt-1.5 text-sm text-gray-500">
              Search Products, Delete Products, Edit Products Keep it up! ðŸš€
            </p>
          </div>
        </div>
      </header>

      <section className="bg-gray-50">
        <div className="container  px-5 py-10 mx-auto ">
          {loading ? (
            <p>loading...</p>
          ) : error ? (
            toast.error(`${error}`)
          ) : (
            <table className="min-w-full divide-y divide-gray-200 text-sm">
              <thead className="bg-gray-200">
                <tr>
                  <th className="whitespace-nowrap px-4 py-2 text-left font-medium text-gray-900">
                    {" "}
                    ID{" "}
                  </th>
                  <th className="whitespace-nowrap px-4 py-2 text-left font-medium text-gray-900">
                    {" "}
                    USER{" "}
                  </th>
                  <th className="whitespace-nowrap px-4 py-2 text-left font-medium text-gray-900">
                    {" "}
                    DATE{" "}
                  </th>
                  <th className="whitespace-nowrap px-4 py-2 text-left font-medium text-gray-900">
                    {" "}
                    Total{" "}
                  </th>
                  <th className="whitespace-nowrap px-4 py-2 text-left font-medium text-gray-900">
                    {" "}
                    PAID{" "}
                  </th>
                  <th className="whitespace-nowrap px-4 py-2 text-left font-medium text-gray-900">
                    {" "}
                    DELIVERED{" "}
                  </th>
                  <th className="whitespace-nowrap px-4 py-2 text-left font-medium text-gray-900"></th>
                </tr>
              </thead>

              <tbody className="divide-y divide-gray-200">
                {orders.length === 0 ? (
                  <p>No orders</p>
                ) : (
                  orders.map((x) => (
                    <>
                      <tr key={x._id}>
                        {x.reported ? (
                          <td className="whitespace-nowrap px-4 py-2 font-medium text-red-900">
                            {x._id} Reported
                          </td>
                        ) : (
                          <td>{x._id}</td>
                        )}

                        {/* className="whitespace-nowrap px-4 py-2 font-medium text-gray-900" */}
                        <td className="whitespace-nowrap px-4 py-2 font-medium text-gray-900">
                          {x.user && x.user.name}
                        </td>
                        <td className="whitespace-nowrap px-4 py-2 font-medium text-gray-900">
                          {x.createdAt.substring(0, 10)}
                        </td>
                        <td className="whitespace-nowrap px-4 py-2 font-medium text-gray-900">
                          PKR{x.totalPrice}
                        </td>
                        <td className="whitespace-nowrap px-4 py-2 font-medium text-gray-900">
                          {x.isPaid ? x.PaidAt : <p> X</p>}
                        </td>
                        <td className="whitespace-nowrap px-4 py-2 font-medium text-gray-900">
                          {x.isDelivered ? (
                            x.deliveredAt.substring(0, 10)
                          ) : (
                            <p> X</p>
                          )}
                        </td>
                        <td className="whitespace-nowrap px-4 py-2 font-medium text-gray-900">
                          <Link to={`/admin/order/${x._id}`}>
                            <button classNameName="btn btn-primary">
                              {" "}
                              Details{" "}
                            </button>
                          </Link>
                        </td>
                        {x.isDelivered && (
                          <>
                            <td className="whitespace-nowrap px-4 py-2 font-medium text-gray-900">
                              <button
                                classNameName="btn btn-primary"
                                onClick={() => deleteHandler(x._id)}
                              >
                                Delete{" "}
                              </button>
                            </td>
                          </>
                        )}
                      </tr>
                    </>
                  ))
                )}
              </tbody>
            </table>
          )}
        </div>
      </section>
    </>
  );
};

export default AdminOrderList;
